@charset "utf-8";

$viewport-width: 1250px;
$wrapper-width: 1200px;
$md-break: 1024px;
$sm-break: 767.98px;
$xs-break: 374px;
$font-sans: "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", "游ゴシック", yugothic, "Yu Gothic", "メイリオ", meiryo, sans-serif;
$font-serif: "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "Hiragino Mincho Pro", "游明朝", yumincho, "Times New Roman", georgia, serif;
$font-size-pc: 0.14rem;
$font-size-sp: 0.14rem;
$font-weight-pc: 400;
$font-weight-sp: 400;
$line-height: calc(20 / 14);
$color-default: #000;
